.project{
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid rgb( 243 , 242, 241);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
} 
.p-browser{
    height: 20px;
    background-color: rgb( 243 , 242, 241);
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 2;
}
.p-circle{
    width: 6px;
    height: 6px;
    background-color: white;
    border-radius: 50%;
    margin: 3px;
}
.p-img{
    width: 100%;
    
    transition: all 10s ease
}
.project:hover .p-img{
    transform: translateY(-100%);
}
@media screen and (max-width: 480px) {
    .project {
      width: 40%;
      height: 20vh;
    }
  }
